@import "/src/data/styles.css";

::-webkit-scrollbar {
	width: 10px;
}

::-webkit-scrollbar-track {
	background-color: var(--secondary-background);
}

::-webkit-scrollbar-thumb {
	background-color: var(--primary-background);
	border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
	background-color: var(--tertiary-background);
}

.page-content {
	position: relative;
	min-height: 100vh;
	background-color: rgba(13, 15, 16, 0.88);	
	padding: 20px;										
}

.page-content:before,
.page-content:after {
	content: "";
	position: absolute;
	top: 0;
	bottom: 0;
	width: calc((100% - 1400px) / 2);

	background-color: var(--secondary-background);				
	border: 1px solid var(--secondary-background);									
	background-image: url(../public/tile.png);									
}

.page-content:before {
	left: 0;
	z-index: 1;
}

.page-content:after {
	right: 0;
	z-index: 1;
}

.content-wrapper {
	max-width: 1200px;
	margin: 0 auto;
}

.title {
	color: var(--primary-color);
	font-family: var(--secondary-font);
	font-size: 45px;
	font-weight: 700;
	width: 70%;
}

.subtitle {
	padding-top: 10px;
	color: var(--secondary-color);
	font-size: 16px;
	line-height: 28px;
	width: 70%;
	margin-top: 25px;
}

.page-footer {
	margin-top: auto;
	position: block;
}

.page-footer::before {
	content: "";
	position: absolute;
	left: 0;
	right: 0;
	border-top: 2px solid var(--tertiary-color);
}

@media (max-width: 1270px) {
	.page-content:before,
	.page-content:after {
		width: calc((100% - 90%) / 2);
	}

	.content-wrapper {
		max-width: 90%;
		padding-left: 10%;
		padding-right: 10%;
	}
}

@media (max-width: 1024px) {
	.page-content:before,
	.page-content:after {
		width: calc((100% - 950px) / 2);
	}

	.content-wrapper {
		max-width: 980px;
	}
}

@media (max-width: 800px) {
	.content-wrapper {
		max-width: 90%;
		margin: 0 auto;
		padding: 0 10px;
	}

	.page-content {
		position: relative;
		min-height: 103vh;
	}

	.page-content:before,
	.page-content:after {
		display: none;
	}

	.title {
		width: 100% !important;
		font-size: 32px !important;
	}

	.subtitle {
		width: 100% !important;
		font-size: 14px !important;
	}
}
