@import "../../data/styles.css";

.homepage-logo-container {
	display: flex;
	justify-content: left;
	align-items: center;
	padding-top: 150px;
}

.homepage-container {
	padding-top: 0px;
}

.homepage-first-area {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.homepage-first-area-left-side {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.homepage-title {
	width: 85% !important;
}

.homepage-subtitle {
	width: 85% !important;
}

.homepage-first-area-right-side {
	display: flex;
	align-items: center;
}

.homepage-image-container {
	width: 370px;
	height: 370px;
}

.homepage-image-wrapper {
	overflow: hidden;
	border-radius: 10%;
}

.homepage-image-wrapper img {
	width: 100%;
}

.homepage-socials {
	display: flex;
	justify-content: left;
	align-items: center;
	padding-top: 100px;
}

.homepage-social-icon {
	padding-right: 20px;
	font-size: 25px;
	transition: color 0.2s ease-in-out;
	color: var(--secondary-color);
}

.homepage-social-icon:hover {
	color: var(--primary-color);
}

.homepage-after-title {
	display: flex;
}

.homepage-work-area {
	justify-content: space-between;
	align-items: center;
	margin-top: 80px;
}

.work-subtitle {
	width: 85% !important;
	margin-bottom: -50px
}

.homepage-works {
	flex-basis: 300px;
}

.homepage-projects-area {
	justify-content: space-between;
	align-items: center;
}

.projects-subtitle {
	width: 85% !important;
	margin-bottom: -50px
}

.homepage-projects {
	flex-basis: 300px;
}

@media (max-width: 1024px) {

	.homepage-logo-container {
		display: flex;
		justify-content: left;
		align-items: center;
		padding-top: 70px;
	}

	.homepage-first-area {
		flex-direction: column;
	}

	.homepage-first-area-left-side {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		width: 100%;
		order: 2;
	}

	.homepage-title {
		width: 100% !important;
		font-size: 36px !important;
		margin-top: 0px;
		order: 2;
	}

	.homepage-subtitle {
		width: 100% !important;
		font-size: 14px !important;
		margin-top: 10px;
		order: 3;
	}

	.homepage-socials {
		display: flex;
		justify-content: left;
		align-items: center;
		padding-top: 40px;
	}

	.homepage-first-area-right-side {
		order: 1;
	}

	.homepage-image-container {
		display: none !important;
	}

	.homepage-after-title {
		flex-direction: column;
	}

	.work-title, .project-title-p {
		font-size: 36px !important;
	}

	.work-subtitle, .project-desc {
		font-size: 14px !important;
	}
	
	.homepage-projects {
		flex-basis: auto;
		margin-top: -60px !important;
		margin-bottom: 60px !important;
	}
}